.result h1 {
    color: #282c34;
}

.result div {
    display: inline-table;
    vertical-align: middle;
    padding: 5px;
}

.dotted {
    border: grey 3px dashed;
    height: 50vh;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.dotted:after {
    content: 'Use the search bar to add a new company to the dashboard :)';
    font-size: 1.5rem;
    color: #ddd;
}

@media screen and (max-width: 370px) {
    .control {
        width: 100%;
        height: fit-content;
    }
    .dotted:after {
        content: 'Search & add a new company :)';
        padding: 3%;
        line-height: 2rem;
    }
}